.movies .movie {
    background-color: white;
    font-weight: 300;
    padding: 20px;
    border-radius: 12px;
    color: #58595e;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    transition: 0.75s ease;
}

.movies .movie a {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    text-decoration: none;
    color: inherit;
}

.movies .movie:hover {
    box-shadow: 0 28px 42px 10px rgba(50, 50, 93, 0.25),
        0 23px 31px -3px rgba(0, 0, 0, 0.3), 0 11px 31px -1px rgba(0, 0, 0, 0.025);
    transform: translateY(-15px);
}

.movie img {
    position: relative;
    top: -50px;
    width: 100%;
    max-width: 150px;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
        0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

@media screen and (max-width: 1090px) {
    .movies .movie a {
        display: grid;
        grid-template-rows: minmax(0, 200px) 2fr;
        text-decoration: none;
        color: inherit;
    }

    .movie img {
        position: relative;
        top: -50px;
        width: 100%;
        max-width: 150px;
        margin-right: 0;
        box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
            0 18px 36px -18px rgba(0, 0, 0, 0.3),
            0 -12px 36px -8px rgba(0, 0, 0, 0.025);
    }
}

.movie .movie__title,
.movie .movie__year {
    margin: 0;
}

.movie .movie__title {
    margin-bottom: 5px;
    font-size: 24px;
    font-weight: 400;
    color: #2c2c2c;
}

.movie .movie__year {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 10px;
}

.movie .movie__genres {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 5px 0px;
    padding: 0;
}

.movie__genres li {
    font-size: 14px;
    font-weight: 500;
    letter-spacing: -1px;
    margin-right: 10px;
    background: #e4e1e1;
    color: #303030;
    padding: 2px 8px;
    border-radius: 6px;
}

.movie__summary {
    word-wrap: break-word;
}

@media screen and (max-width: 1090px) {
    .movie__genres li {
        font-size: 12px;
        margin-right: 4px;
    }
}